import { Injectable } from '@angular/core';
import { Observable, fromEvent, merge } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ScriptService {
  constructor() {
  }

  load(uri: string): Observable<{}> {
    return this.loadScript(uri);
  }

  private loadScript(uri: string): Observable<{}> {
    const body: HTMLElement = document.body;
    const script: HTMLScriptElement = Object.assign(document.createElement('script'), { type: 'text/javascript', async: true, defer: true, src: uri });

    const errorEvents$ = fromEvent(script, 'error');
    const loadEvents$ = fromEvent(script, 'load')
      .pipe(e => { body.appendChild(script); return e; })
      .pipe(e => { body.removeChild(script); return e; });

    return merge(loadEvents$, errorEvents$);
  }
}

