/// <reference types="@types/googlemaps" />

import { Injectable } from '@angular/core';
import { MapConfig } from '../models/map-config.interface';
import { FileService } from './file.service';
import { ScriptService } from './script.service';

@Injectable({
  providedIn: 'root'
})
export class MapElementService {
  private _configFile: string = '/assets/map.config.json';
  private _element: HTMLDivElement;
  private _map: google.maps.Map;

  constructor(private _scriptService: ScriptService,
    private _fileService: FileService) {
  }

  public initilize(element: HTMLDivElement): void {
    this._element = element;
    this.initilizeMap();
  }

  private initilizeMap(): void {
    this._fileService.load(this._configFile).subscribe(resolve => {
      const mapConfig = resolve.json() as MapConfig;
      this.removeRobotoFromHead();
      this.loadMapScript(mapConfig.api.uri);
    })
  }

  private removeRobotoFromHead(): void {
    const head = document.head;
    const insertBefore = head.insertBefore;
    let removed = false;

    head.insertBefore = (newElement, referenceElement) => {
      let element = (<any>newElement) as HTMLLinkElement;
      if (element && element.href && element.href.indexOf('https://fonts.googleapis.com/css?family=Roboto') === 0) {
        removed = true;
        return;
      }

      insertBefore.call(head, newElement, referenceElement);
      if (removed) {
        head.insertBefore = insertBefore;
        return;
      }

      return newElement;
    };
  }

  private loadMapScript(uri: string): void {
    this._scriptService.load(uri).subscribe(
      () => { this.createMap(); },
      reject => { console.log(reject); });
  }

  private createMap(): void {
    const options = {
      zoom: 18,
      center: { lat: 53.34206247835682, lng: 83.76607192698907 },
      mapTypeControl: false,
      zoomControl: false,
      scaleControl: false,
      streetViewControl: false,
      fullscreenControl: false,
      disableDefaultUI: true,
      clickableIcons: false,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      gestureHandling: 'none' as google.maps.GestureHandlingOptions
    };

    this._map = new google.maps.Map(this._element, options);
  }
}
