import { Component, OnInit } from '@angular/core';
import { Products } from '../../shared/models/product.interface';
import { FileService } from '../../shared/service/file.service';

@Component({
  selector: 'app-carusel',
  templateUrl: './carusel.component.html',
  styleUrls: ['./carusel.component.css']
})
export class CaruselComponent implements OnInit {
  private _settingsFile: string = '/assets/settings.json';
  product: Products;

  constructor(private _fileService: FileService) { }

  ngOnInit(): void {
    this.initilizeProducts();
  }

  private initilizeProducts(): void {
    this._fileService.load(this._settingsFile).subscribe(resolve => {
      this.product = resolve.json() as Products;
    })
  }
}
