import { Directive, ElementRef, OnInit } from '@angular/core';
import { MapElementService } from '../service/map-element.service';

@Directive({
  selector: '[appGoogleMap]'
})
export class GoogleMapDirective implements OnInit {
  private _element: HTMLDivElement;

  constructor(elRef: ElementRef,
    private _mapElementService: MapElementService) {
    this._element = elRef.nativeElement;
  }

  ngOnInit(): void {
    this._mapElementService.initilize(this._element);
  }
}
